// axios.all
axios
  .all([
    axios.get('/get', { params: { name: 'why' } }),
    axios.post('/post', { params: { age: 18 } })
  ])
  .then((res) => {
    console.log(res[0].data)
    console.log(res[1].data)
  })

// axios拦截器(拦截请求、拦截响应)
// fn1:请求发送成功执行的函数 fn2:请求发送失败时执行的函数
axios.interceptors.request.use(
  (config) => {
    // 这里可以对config进行修改
    // 1.给请求添加token
    // 2.isLoading动画
    console.log('请求成功的拦截')
    return config
  },
  (err) => {
    console.log('请求发送失败')
    return err
  }
)
// fn1:数据响应成功(服务器正常的返回了数据 20x)
// fn2:响应失败
axios.interceptors.response.use(
  (res) => {
    console.log('响应成功的拦截')
    return res.data
  },
  (err) => {
    console.log('响应失败')
    return err
  }
)
